import React from 'react';
import { useTexture } from '@react-three/drei';

const MeCube = () => {
	// Load the texture. This is a single texture that will be placed on all 6 sides of the cube
	const [cube1, cube2, cube3, cube4, cube5] = useTexture([
		'/cube1.png',
		'/cube2.png',
		'/cube3.png',
		'/cube4.png',
		'/cube5.png'
	]);
	return (
		<mesh visible position={[0, 0, 0]} rotation={[10, 20, 0]}>
			<boxGeometry attach="geometry" args={[1, 1, 1]} />
			<meshBasicMaterial attachArray="material" map={cube1} />
			<meshBasicMaterial attachArray="material" map={cube2} />
			<meshBasicMaterial attachArray="material" map={cube3} />
			<meshBasicMaterial attachArray="material" map={cube4} />
			<meshBasicMaterial attachArray="material" map={cube5} />
			<meshBasicMaterial attachArray="material" map={cube5} />
		</mesh>
	);
};

export default MeCube;
